.dropdown-common-xx {
    display: inline-flex;
    align-items: center;
    background-color: #EBEBEB;
    border-radius: 48px;
    padding-left: 10px;
    outline: none;
    cursor: pointer;
    border: 1px solid transparent;
    position: relative;
    margin-right: 15px;
    &:focus {
        border: 1px solid #ccc;
    }
    ul.dropdown-target-container {
        position: absolute;
        transition: .3s;
        top: 90%;
        list-style-type: none;
        left: 2%;
        margin: 0;
        padding: 5px 0;
        min-width: 100px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        z-index: 100;
        max-height: 150px;
        background: #fff;
        overflow: auto;
        .dropdown-list-item-xx {
            padding: 5px;
            border-radius: 5px;
            margin: 2px;
            font-size: 12px;
            border: 1px solid #ccc;
            &.selected {
                border: 1px solid #33F;
            }
        }
        .dropdown-button-xx {
            border: none;
            padding: 0;
            margin: 0;
            outline: none;
            background: transparent;
            width: 100%;
            text-align: center;
        }
    }
}